.containerHomeDos{
    min-width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../img/fondoNew.png');
    background-size: cover;
    background-position: center;

    flex-wrap: wrap;

    .logoContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        min-height: 10vh;
    }
    .subtitleContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;        
        max-width: 85vw;
        display: flex;
        justify-content: center;
        align-items: center;        
        min-height: 10vh;
        flex-wrap: wrap;
        
        text{
            text-align: center;
            font-size: 22px;
            color: #FFF;
            strong{
                margin-left: 5px;
                margin-right: 5px;
            }
        }
    }
    .formContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        max-width: 85vw;
        min-height: 50vh;
        flex-wrap: wrap;
        input {
            border: none;
            min-width: 82vw;
            min-height: 30px;
            max-height: 30px;
            border-radius: 5px;
            padding-left: 2vw;
        }
        .containerBotonSubmenu{
            min-width: 85vw;
            min-height: 30px;
            position: absolute;
            margin-top:calc(40vh - 12px);
            display: flex;
            justify-content: flex-end;
            align-items: center;
            .boton{
                background-color: #D18919;
                min-width: 20px;
                max-width: 20px;
                min-height: 20px;
                max-height: 20px;
                margin-right: 10px;
                border-radius: 5px;
                display: flex;
                justify-content: center;
                align-items: center;
                .lineaUno{
                    position: absolute;
                    min-height: 1px;
                    min-width: 10px;
                    rotate: 45deg;
                    margin-right: 6px;
                    background-color: #FFF;
                }
                .lineaDos{
                    position: absolute;
                    min-height: 1px;
                    min-width: 10px;
                    margin-left: 6px;
                    rotate: -45deg;
                    background-color: #FFF;
                }

            }
        }
    }
    .buttonContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        min-height: 6vh;
       
        button{
            border: none;
            min-width: 85vw;
            min-height: 30px;
            border-radius: 15px;
            background-color: #D18919;
            color: #FFF;
            font-size: 15px;
        }
    }
    .footerContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        min-height: 8vh;
        max-height: 8vh;
    
        .logos{
            min-width: 50vw;
            display: flex;
            min-height: 6vh;
            justify-content: space-around;
            align-items: center;
            img{
                min-width: 20px;
            }

        }
    }
    .espacioContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;
        min-height: 8vh;
    }
    .subFormulario{
        position:  absolute;
        min-width: 85vw;
        min-height: 5vh;

        ul{
            min-width: 85vw;
            max-width: 85vw;
            border-radius: 8px;
            min-height: 0vh;            
            margin: 0px;
            padding: 0px;
            padding-top: 5vh;
            padding-bottom: 5vh;
            background-color: #FFF;
            display: flex;
            justify-content: center;
            align-items: ce0nter;
            flex-wrap: wrap;
            display: none;
            li{
                font-size: 15px;
                list-style: none;
                display: flex;
                min-width: 80vw;                
                max-width: 80vw;
                border-bottom: 1px solid rgba(0,0,0,0.3);
                margin-bottom: 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                display: none;
                div{
                    margin-right: 8px;
                    min-width: 8px;
                    max-width: 8px;
                    min-height: 8px;
                    max-height: 8px;
                    background-color: #D18919;
                    border-radius: 4px;
                }
                text{
                    display: flex;
                    min-width: 75vw;
                    flex-direction: column;
                    
                    .titulo{
                        color: #D18919;
                    }
                }
            }
    
        }
    }



}



